import banner from '../../components/banner.vue'
export default{
  components: {
    banner: banner
  },
  data() {
    return {
      swiperList: '',
      categorys: [{
        name: '未使用',
        type: 'not_use'
      }, {
        name: '已使用',
        type: 'is_use'
      }, {
        name: '已过期',
        type: 'is_expire'
      }],
      data_type: 'not_use',           // 默认选中第一个分类
      data_index: 0,                  // 默认选中第一个索引
      list: ''
    }
  },
  onLoad() {
    this.getBanners()
    this.getCouponList()
  },
  methods: {
    
    // 获取轮播图
    getBanners() {
      let that= this,
        params= {
          url: that.$api.couponBanners
        }
      that.$httpRequest(params).then(res => {
        that.swiperList= res.data.list
      })
    },
    
    // 选择分类
    selectTab(type, index) {
      let that= this
      that.data_type= type
      that.data_index= index
      that.getCouponList(type)
    },
    
    // 优惠券列表
    getCouponList(type) {
      let that= this,
        params= {
          url: that.$api.myCoupon,
          data: {
            data_type: type?type: 'not_use'
          }
        }
      that.$httpRequest(params).then(res => {
        console.log(res)
        that.list= res.data.list
      })
    },
    
    // 领券
    getCoupon(id, index) {
      let that= this
      if(index > 0) {
        return
      }
      uni.navigateTo({
        url: './couponDetail?id=' + id
      })
    }
  },
  
  
  
    
}
